.paises{
    height: fit-content;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 0px 60px;
}

@media only screen and (max-width: 550px) {
    .paises{
        justify-content: center;
        padding: 0px 20px;
    }
}
