.nav{
    box-sizing: border-box;
    width: 100%;
    height: 60px;
    padding: 0px 30px;

    border-bottom: 2px solid lightgrey;

    display: flex;
    justify-content: space-between;
    align-items: center;
}
.dark-nav{
    background-color: hsl(209, 23%, 22%);
    border-bottom: 2px solid hsl(208, 23%, 15%);
}

.titulo{
    /* width: 55%; */
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 800;

    text-decoration: none !important;
    color: black;
}
.dark-titulo{
    color: hsl(0, 0%, 100%);
}

div.darkmode{
    font-family: 'Nunito Sans', sans-serif;
    font-weight: 300;

    display: flex;
    justify-content: flex-end;
    align-items: center;
}
#lua{
    width: 20px !important;
    margin-right: -5px;
}


.darkmode:hover{
    cursor: pointer;
}

div.darkmode p{
    margin-left: 10px;
}
/* 300, 600, 800 */
/* font-family: 'Nunito Sans', sans-serif; */

.card{
    font-family: 'Nunito Sans';
    width: 21%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 20px;

    background-color: white;

    min-height: 300px;

    box-shadow: 0 0 10px 5px lightgrey;

    border-radius: 5px;

    text-decoration: none !important;
    color: black !important;
}
.dark-card{
    background-color: hsl(209, 23%, 22%);
    box-shadow: none !important;
}

.card:hover{
    cursor: pointer;
}

.card img{
    width: 100%;
    max-height: 168px;
    object-fit: cover;
    margin: 0;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
}

.infos{
    /* box-sizing: border-box; */
    width: calc(100% - 40px);
    height: calc(100% - 40px);

    padding: 20px;

    background-color: white;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}
.dark-infos{
    background-color: hsl(209, 23%, 22%) !important;

    color: hsl(0, 0%, 100%);
}

.topico{
    font-weight: 600;
}

@media (max-width: 850px){
    .card{
        width: 45%;
    }
}

@media only screen and (max-width: 550px) {
    .card{
        width: 90%;
    }
}

