/* 300, 600, 800 */
/* font-family: 'Nunito Sans', sans-serif; */

input:focus{
    outline: 0;
}

.form{
    box-sizing: border-box;
    width: 100%;
    padding: 40px 0px;
    
    display: flex;
    justify-content: space-between;    
}

.search{
    height: 40px;
    width: 30%;
    padding-left: 40px;

    border-radius: 4px;
    border: 1px solid hsl(0,0%,80%);
    box-shadow: 1px 2px 5px lightgrey;

    background-image: url(/static/media/lupa.03b4590a.svg);
    background-repeat: no-repeat;
    background-size: 20px;
    background-position: 10px;

    font-family: 'Nunito Sans', sans-serif;
    font-size: 12px;
}
.dark-search{
    background-color: hsl(209, 23%, 22%);
    color: hsl(0, 0%, 100%);
    box-shadow: none;
    border: none;
}
.dark-search::-webkit-input-placeholder{
    color: white;
}
.dark-search:-ms-input-placeholder{
    color: white;
}
.dark-search::-ms-input-placeholder{
    color: white;
}
.dark-search::placeholder{
    color: white;
}

.lista{
    width: 170px;
    height: -webkit-fit-content !important;
    height: -moz-fit-content !important;
    height: fit-content !important;
    border-radius: 5px;
    border: none;
    box-shadow: 1px 2px 5px lightgrey;

    font-family: 'Nunito Sans', sans-serif;
    font-size: 12px;
    font-weight: 600;
}

.dark-lista{
    box-shadow: none;
}
.option:active{
    background-color: lightgrey !important;
}
.option:hover{
    background-color: lightgrey !important;
}
.dark-option:active{
    background-color: blue;
}
.dark-option:hover{
    background-color: blue;
}

.back{
    display: flex;
    align-items: center;
    justify-content: center;

    width: 90px;

    background-color: white;

    border: 1px solid lightgrey;
    border-radius: 5px;
    box-shadow: 0 0 10px 1px lightgrey;

    text-decoration: none;
    color: black;
    font-family: 'Nunito Sans', sans-serif;
    font-size: 14px;

    padding: 5px 0px;
}
.dark-back{
    background-color: hsl(209, 23%, 22%);
    box-shadow: none;
    border: 1px solid hsl(206, 22%, 13%);
    color: hsl(0, 0%, 100%);
}
.back > *{
    margin-right: 5px;
}

@media only screen and (max-width: 600px) {
    .form{
        flex-direction: column;
    }
    .search{
        width: calc(100% - 40px);
        margin-bottom: 15px;
    }
    .lista{
        width: 100%;
    }
    .dark-lista{
        width: 100%;
    }
}
