/* 300, 600, 800 */
/* font-family: 'Nunito Sans', sans-serif; */

.bigcountry{
    width: 100%;
    height: calc(100vh - 190px);

    padding-bottom: 20px;

    display: flex;
}

.bigcountry img{
    width: 40%;
    height: 23vw;
}

.countryinfos{
    font-family: 'Nunito Sans', sans-serif, Arial, Helvetica, sans-serif;
    margin-left: 5%;
}

.countryinfos h2{
    margin-top: 25px;
    margin-bottom: 20px;
}

.textinfos{
    display: flex;
    font-size: 1.8vw;
    line-height: 35px;
}
.dark-textinfos{
    color: hsl(0, 0%, 100%);
}

.otherinfos{
    margin-left: 2%;
}

.bordercountries{
    margin-top: 15px;
}

.topicoborder{
    display: inline-block;
    font-size: 1.5vw;
}

.bordernames{
    display: flex;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    flex-wrap: wrap;
}

.border{
    display: inline-block;
    padding: 3px 15px;
    border: 1px solid lightgrey;
    box-shadow: 0 0 10px 1px lightgrey;
    margin: 5px 5px;
    font-size: 1.5vw;
}
.dark-border{
    border: 1px solid hsl(209, 23%, 22%);
    box-shadow: 0 0 10px 1px hsl(209, 23%, 22%);
}

@media only screen and (max-width: 800px) {
    .bigcountry{
        flex-direction: column;
        height: -webkit-fit-content;
        height: -moz-fit-content;
        height: fit-content;
    }
    .bigcountry img{
        width: 100%;
        height: unset;
    }
    .countryinfos{
        margin-left: 2%;
    }
    .textinfos{
        flex-direction: column;
        font-size: 3.5vw;
        line-height: 2em;
    }
    .firstinfos{
        margin-bottom: 30px;
    }
    .otherinfos{
        margin-left: 0;
    }
    .border{
        font-size: 2.5vw;
    }
    .topicoborder{
        font-size: 2.5vw;
    }
}
/* 600, 600, 800 */
